<template>
	<div class="pageWrap navBarPadding" :style='"min-height:"+bgWrapHeight+"px;"'>
        <div class="navBarBox" ref="navBarHeight">
            <img class='navLeftArrowIcon' src="../assets/images/headerBack.png" alt="" @click='goBack()'>
            <p class='navTitle'>Balance History</p>
        </div>
        <div class="classTypeBox">
            <div class="classTypeItem"  @click="selectVideoType('ALL')">
                <p class="classTypeName" :style="selectVideoTypeId == 'ALL' ? 'color:#FF30DF;':''">{{'ALL'}}</p>
                <span class="classSelectBorder" v-if="selectVideoTypeId == 'ALL'"></span>
            </div>
            <div class="classTypeItem"  @click="selectVideoType('Recharge')">
                <p class="classTypeName" :style="selectVideoTypeId == 'Recharge' ? 'color:#FF30DF;':''">{{'Recharge'}}</p>
                <span class="classSelectBorder" v-if="selectVideoTypeId == 'Recharge'"></span>
            </div>
            <div class="classTypeItem"  @click="selectVideoType('Withdraw')">
                <p class="classTypeName" :style="selectVideoTypeId == 'Withdraw' ? 'color:#FF30DF;':''">{{'Withdraw'}}</p>
                <span class="classSelectBorder" v-if="selectVideoTypeId == 'Withdraw'"></span>
            </div>
        </div>
        <div class="orderDetailWrap" style="margin-top: .72rem;">
            <van-list
                v-model="allListLoading"
                :finished="allFinished"
                loading-text='Loading...'
                @load="getAllData()"
                v-show="selectVideoTypeId == 'ALL'"
            >
                <div class="whiteItemBox" v-for="(item,index) in allListData" :key="index" @click="goPage('/VideoDetails/'+item.id)">
                    <p class="balanceTitle">{{item.title ? item.title : '--'}}</p>
                    <div class="balanceMainBox flexBetween" style="margin: .02rem 0;">
                        <span class='balanceNO'>{{'No.'+item.id}}</span>
                        <span class='balanceNumber flexStart' :style="item.amount.indexOf('-') == -1 ? '' : 'color:#38ADFF;'">
                            <span>{{item.amount.indexOf('-') == -1 ? '+' : ''}}</span>
                            {{item.amount}}
                        </span>
                    </div>
                    <p class="balanceNO">{{item.time}}</p>
                </div>
                <div class="finishedBox flexCenter" v-if="allFinished && allListPage > 2">
                    <div class="huiBorder"></div>
                    <div class="finishedText">Unable to view records from 60 days ago</div>
                </div>
            </van-list>
            <van-list
                v-model="rechargeListLoading"
                :finished="rechargeFinished"
                loading-text='Loading...'
                @load="getRechargeData()"
                v-show="selectVideoTypeId == 'Recharge'"
            >
                <div class="whiteItemBox" v-for="(item,index) in rechargeListData" :key="index" >
                    <div class="statusLabel" v-if="item.status == 'pending'">Pending</div>
                    <div class="statusLabel huiStatus" v-else-if="item.status == 'cancelled'">Cancelled</div>
                    <div class="statusLabel failedStatus" v-else-if="item.status == 'failed'">Error</div>
                    <div class="statusLabel huiStatus" v-else-if="item.status == 'completed'">Completed</div>
                    <p class="balanceTitle">{{item.title ? item.title : '--'}}</p>
                    <div class="balanceMainBox flexBetween" style="margin: .02rem 0;">
                        <span class='balanceNO'>{{'No.'+item.id}}</span>
                        <span class='balanceNumber flexStart'>
                            <span>{{'+'}}</span>
                            {{item.price}}
                        </span>
                    </div>
                    <p class="balanceNO">{{item.time}}</p>
                </div>
                <div class="finishedBox flexCenter" v-if="rechargeFinished && rechargeListPage > 2">
                    <div class="huiBorder"></div>
                    <div class="finishedText">Unable to view records from 60 days ago</div>
                </div>
            </van-list>
            <van-list
                v-model="withdrawListLoading"
                :finished="withdrawFinished"
                loading-text='Loading...'
                @load="getWithdrawData()"
                v-show="selectVideoTypeId == 'Withdraw'"
            >
                <template v-if="withdrawListData.length > 0">
                    <div class="whiteItemBox" v-for="(item,index) in withdrawListData" :key="index" @click="goPage('/VideoDetails/'+item.id)">
                        <div class="statusLabel" v-if="item.status == 'pending'">Pending</div>
                        <div class="statusLabel huiStatus" v-else-if="item.status == 'cancelled'">Cancelled</div>
                        <div class="statusLabel failedStatus" v-else-if="item.status == 'error'">Error</div>
                        <div class="statusLabel failedStatus" v-else-if="item.status == 'revoked'">Revoked</div>
                        <div class="statusLabel publishedStatus" v-else-if="item.status == 'remitted'">Remitted</div>
                        <p class="balanceTitle">{{item.title ? item.title : '--'}}</p>
                        <div class="balanceMainBox flexBetween" style="margin: .02rem 0;">
                            <span class='balanceNO'>{{'No.'+item.id}}</span>
                            <span class='balanceNumber flexStart' style="color:#38ADFF">
                                <span>-</span>
                                {{item.amount}}
                            </span>
                        </div>
                        <p class="balanceNO">{{item.time}}</p>
                    </div>
                    <div class="finishedBox flexCenter" v-if="withdrawFinished && withdrawListPage > 2">
                        <div class="huiBorder"></div>
                        <div class="finishedText">Unable to view records from 60 days ago</div>
                    </div>
                </template>
                <template v-else>
                    <div class="noRecordBox flexCenter">
                        <img class='noRecordIcon' src="../assets/images/noRecordIcon.png" alt="">
                        <p class="noRecordText">No record</p>
                    </div>
                </template>
            </van-list>
        </div>
	</div>
</template>
<script>
	import https from '../api/https'
	export default {
		name: 'BalanceHistory',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                selectVideoTypeId: 'ALL',

                allListData: [],
                allListLoading: false,
                allFinished: false,
                allSkipId: '',
                allListPage: 1,

                rechargeListData: [],
                rechargeListLoading: false,
                rechargeFinished: false,
                rechargeSkipId: '',
                rechargeListPage: 1,

                withdrawListData: [],
                withdrawListLoading: false,
                withdrawFinished: false,
                withdrawSkipId: '',
                withdrawListPage: 1,
			}
		},
		created(){
			this.getRechargeData()
			this.getWithdrawData()
            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getAllData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    skipId: this.allSkipId
                }
				https.fetchGet('/api/user/history/balance',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.allListData = this.allListData.concat(res.data.list)
                        this.allListLoading = false
                        this.allListPage ++
                        if(res.data.list.length > 0) {
                            this.allSkipId = res.data.list[res.data.list.length - 1].id
                        }
                        if(!res.data.hasNext) {
                            this.allFinished = true
                            console.log('finished')
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getAllData()
					}
				})
			},
            getRechargeData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'recharge',
                    skipId: this.rechargeSkipId
                }
				https.fetchGet('/api/pay/cashier/record',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.rechargeListData = this.rechargeListData.concat(res.data.list)
                        this.rechargeListLoading = false
                        this.rechargeListPage ++
                        if(res.data.list.length > 0) {
                            this.rechargeSkipId = res.data.list[res.data.list.length - 1].id
                        }
                        if(!res.data.hasNext) {
                            this.rechargeFinished = true
                            console.log('finished')
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getRechargeData()
					}
				})
			},
            getWithdrawData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    skipId: this.withdrawSkipId
                }
				https.fetchGet('/api/pay/withdraw/record',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.withdrawListData = this.withdrawListData.concat(res.data.list)
                        this.withdrawListLoading = false
                        this.withdrawListPage ++
                        if(res.data.list.length > 0) {
                            this.withdrawSkipId = res.data.list[res.data.list.length - 1].id
                        }
                        if(!res.data.hasNext) {
                            this.withdrawFinished = true
                            console.log('finished')
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getWithdrawData()
					}
				})
			},
            selectVideoType(item){
                this.selectVideoTypeId = item
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
<style scoped>
