        position: absolute;
        top: 0;
        right: 0;
        height: .36rem;
        background: #38ADFF;
        border-radius:.05rem;
        padding: .05rem .24rem;
        box-sizing: border-box;
        font-size: .24rem;
        font-weight: 500;
        color: #FFFFFF;
        line-height: .28rem;
    }
    .pageWrap {
        background: #EAF1F7;
    }
	.orderDetailWrap {
        padding: .18rem .24rem 1.45rem;
