    }
    .balanceNO {
        font-size: .25rem;
        color: #999EA5;
        line-height: .31rem;
    }
    .balanceNumber {
        font-size: .40rem;
        font-weight: 500;
        color: #FF8D28;
        line-height: .49rem;
    }
    .finishedBox {
        position: relative;
        margin-top: .28rem;
    }
    .huiBorder {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
