        background: #CED8E4;
        border-radius: .02rem;
    }
    .finishedText {
        position: relative;
        z-index: 1;
        padding: 0 .34rem;
        font-size: .24rem;
        color: #AFC0CA;
        line-height: .28rem;
        text-align: center;
        overflow: hidden;
        background: #EAF1F7;
    }
    .classTypeBox {
        position: fixed;
        top: .86rem;
        left: 0;
        width: 100%;
        height: .72rem;
        display: flex;
        justify-content: start;
        align-items: center;
        background: #fff;
        border-bottom: .01rem solid #CED8E4;
        z-index: 1000;
    }
    .classTypeItem {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        height: 100%;
        width: 50%;
    }
    .classTypeName {
        font-size: .29rem;
        font-weight: 500;
        color: #BAC3CC;
