    }
    .classSelectBorder {
        position: absolute;
        left: 0;
        bottom: -.01rem;
        width: 100%;
        height: .09rem;
        background: linear-gradient(310deg, #746CFF 0%, #C72FFF 36%, #FF34C6 100%);
    }
    .whiteItemBox {
        position: relative;
        padding: .27rem;
        background: #fff;
        border-radius: .18rem;
        margin-bottom: .18rem;
    }
</style>